// React e ReactDOM importados via wp-element
// WP usa sua própria versão, evitando que React/DOM seja inserido em cada arquivo do plugin
import React, {useState} from 'react'
import ReactDOM from 'react-dom'
import "./frontend.scss"

const divsToUpdate = document.querySelectorAll('.paying-attention-update-me');

divsToUpdate.forEach(
	div => {
		const data = JSON.parse(div.querySelector("pre").innerHTML)
		ReactDOM.render(<Quiz {...data} />, div)
		div.classList.remove('paying-attention-update-me')
	}
)

function Quiz(props){

	const [isCorrect, setIsCorrect] = useState(undefined);

	function handleAnswer(index){
		if( index == props.correctAnswer){
			setIsCorrect(true)
		}else{
			setIsCorrect(false)
		}
	}

	return(
		<div className="paying-attention-frontend">
			<p>{props.question}</p>
			<ul>
				{props.answers.map( (answer, index) =>
				<li onClick={() => handleAnswer(index)}>{answer}</li>)
				}
			</ul>
			<div className={"correct-message" + (isCorrect === true ? " correct-message--visible" : "")}>
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" className="bi bi-emoji-smile" viewBox="0 0 16 16">
					<path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
					<path d="M4.285 9.567a.5.5 0 0 1 .683.183A3.498 3.498 0 0 0 8 11.5a3.498 3.498 0 0 0 3.032-1.75.5.5 0 1 1 .866.5A4.498 4.498 0 0 1 8 12.5a4.498 4.498 0 0 1-3.898-2.25.5.5 0 0 1 .183-.683zM7 6.5C7 7.328 6.552 8 6 8s-1-.672-1-1.5S5.448 5 6 5s1 .672 1 1.5zm4 0c0 .828-.448 1.5-1 1.5s-1-.672-1-1.5S9.448 5 10 5s1 .672 1 1.5z"/>
				</svg>
				<p>That is correct</p>
			</div>
			<div className={"incorrect-message" + (isCorrect === false ? " correct-message--visible" : "")}>
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" className="bi bi-emoji-smile-upside-down" viewBox="0 0 16 16">
					<path d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zm0-1a8 8 0 1 1 0 16A8 8 0 0 1 8 0z"/>
					<path d="M4.285 6.433a.5.5 0 0 0 .683-.183A3.498 3.498 0 0 1 8 4.5c1.295 0 2.426.703 3.032 1.75a.5.5 0 0 0 .866-.5A4.498 4.498 0 0 0 8 3.5a4.5 4.5 0 0 0-3.898 2.25.5.5 0 0 0 .183.683zM7 9.5C7 8.672 6.552 8 6 8s-1 .672-1 1.5.448 1.5 1 1.5 1-.672 1-1.5zm4 0c0-.828-.448-1.5-1-1.5s-1 .672-1 1.5.448 1.5 1 1.5 1-.672 1-1.5z"/>
				</svg>
				<p>Sorry, try again</p>
			</div>
		</div>
	)
}